* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  background-color: #97C1A9;
  height: 100vh;
}

Button {
  border-radius: 5px;
  border: none;
  font-size: 1rem;
  background-color: #F2CC8F;
  margin-left: 10px;
  white-space: nowrap;
  padding: 5px 10px;
  cursor: pointer;
}
.App {
  width: 90%;
  height: 90vh;
  margin: 5vh auto;
  display: flex;
  flex-flow: column nowrap;
  justify-content: start;
  align-items: center;
  padding: 20px;
  border-radius: 5px;
  background-color: #ECEAE4;
}

.App form {
  bottom: 20px;
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  margin-bottom: 10px;
}

.App form input {
  width: 100%;
  border-radius: 5px;
  border: none;
  font-size: 1rem;
  padding: 5px 10px;
}

.App form button:hover {
  background-color: rgb(189, 166, 129);
}

.App .MemoList {
  width: 100%;
  height: 100%;
  padding: 5px;
  overflow-x: hidden;
  overflow-y: auto;
  display: flex;
  flex-flow: column nowrap;
  margin-bottom: 10px;
  border-radius: 5px;
  background-color: #f5f4f0;
}

.App .MemoList li {
  width: 100%;
  padding: 5px;
  margin-bottom: 5px;
  border-radius: 5px;
  background-color: #F2CC8F;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: start;
}

.App .MemoList li span {
  text-align: left;
  font-size: 1rem;
  margin-right: 5px;
  display: block;
  white-space: warp;
  word-break: break-word;
  border-radius: 5px;
  width: 100%;
}

.App .MemoList li .Delete {
  background-color: #FCB9AA;
}

.App .MemoList li .Copy {
  background-color: #A7DCE4;
}

.App .Control {
  width: 100%;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  align-items: flex-end;
  margin-top: auto;
}

.App .Control button {
  background-color: #FCB9AA;
  margin-left: 10px;

}

@media screen and (max-width: 786px) {
  .App {
    width: 100%;
    height: 100vh;
    margin: 0;
    border-radius: 0;
  }
}